import { useState, useRef } from "react";
import CandlestickChart from "./CandlestickChart";

const CandlestickWrapper = ({options, setOptions, chartRef}) => {
  
  // const [options, setOptions] = useState({
  //   chart: { type: "candlestick" },
  //   series: [
  //     {
  //       name: "Stock Price",
  //       data: [[1701388800000, 100, 500, 100.71, 100.91]], // Initial data
  //     },
  //   ],
  //   xAxis: {
  //       type: "datetime", // Make sure x-axis uses datetime type
  //       labels: {
  //         formatter: function () {
  //           const date = new Date(this.value);
  //           return date.toLocaleDateString(); // Formats the date as 'MM/DD/YYYY'
  //         },
  //       },
  //     }
  // });

  // Get the last data point
  const getLastDataPoint = (dataArray) => {
    return dataArray[dataArray.length - 1];
  };

  // Convert timestamp to a Date object
  const getDate = (date) => {
    return new Date(date);
  };

  // Manipulate date (e.g., add days)
  const manipDate = (date, isReturnMili, amount) => {
    const copyDate = new Date(date);
    copyDate.setDate(copyDate.getDate() + amount);
    if (isReturnMili) return copyDate.getTime();
    return copyDate;
  };

  // Add a new data point
  const addOptions = () => {
    const copyOptions = { ...options };
    const optionData = copyOptions.series[0]?.data;
    const last = getLastDataPoint(optionData);

    const workableDate = getDate(last[0]);
    const newDate = manipDate(workableDate, true, 1); // Increment the date by 1 day

    // New data point logic (last close price as open, close adjusted by +5)
    const newDataPoint = [newDate, last[4], last[2], last[3], last[4] - 50];

    // Add the new data point to the series
    optionData.push(newDataPoint);

    // Update the options state with the new data
    copyOptions.series[0].data = optionData;
    setOptions(copyOptions);

    // If the chart is initialized, add the new point directly to the chart
    if (chartRef.current) {
      const chart = chartRef.current.chart;
      chart.series[0].addPoint(newDataPoint, true); // Add new point to the chart
    }
  };

  const updateLast = () => {
    const copyOptions = { ...options };
    const optionData = copyOptions.series[0]?.data;
    const last = getLastDataPoint(optionData);

    const newDataPoint = [last[0], last[4], last[2], last[3], last[4]+50];
    optionData[optionData.length - 1] = newDataPoint
    copyOptions.series[0].data = optionData;
    setOptions(copyOptions);

    if (chartRef.current) {
        const chart = chartRef.current.chart;
        chart.series[0].addPoint(newDataPoint, true); // Add new point to the chart
      }
  }


  return (
    <>
      <CandlestickChart options={options} chartRef={chartRef} />
      {/* <button onClick={addOptions}>Add Data Point</button>
      <button onClick={updateLast}>Update Last</button> */}
    </>
  );
}

export default CandlestickWrapper