import {useState, useEffect} from "react"
import GameUI from "./GameUI"
import GameLoading from "./GameLoading"

const GameWrapper = ({gameState, setGameState}) => {

if (!gameState.gameData?.stocks || gameState.gameData.stocks.length == 0)
    return (
        <GameLoading gameState={gameState} setGameState={setGameState}/>
    )

    return (
        <GameUI gameState={gameState} setGameState={setGameState}/>
    )
}

export default GameWrapper