import { useState, useEffect } from "react";
import stocks from "../Game/data/stocks";

const GameLoading = ({ gameState, setGameState }) => {
  const [currentStock, setCurrentStock] = useState(null);
  const [stockData, setStockData] = useState(null); // Initially null to indicate no data

  const createInitialStockData = () => {
    const generatedStocks = {};

    Object.keys(stocks).forEach((key) => {
      const stock = stocks[key];
      const startDate = new Date(stock.startYear, 0, 1).getTime(); // January 1 of start year
      const initialPrice = stock.startPrice;

      // Initial stock data array
      generatedStocks[key] = [
        [
          startDate, // Date
          initialPrice, // Open
          initialPrice, // High
          initialPrice, // Low
          initialPrice, // Close
        ],
      ];
    });

    return generatedStocks;
  };

  const generateDailyData = (
    stockArray,
    startYear,
    endYear,
    avg,
    volatility,
    initialTrend
  ) => {
    let currentPrice = stockArray[0][4]; // Start with the first "Close" price
    let currentTrend = initialTrend; // Start with the initial trend ("up" or "down")
  
    for (let year = startYear; year < endYear; year++) {
      for (let day = 0; day < 365; day++) {
        const newDate = new Date(year, 0, 1 + day).getTime();
  
        // Randomly change the trend based on volatility and a random chance
        if (Math.random() < volatility) {
          currentTrend = currentTrend === "up" ? "down" : "up"; // Flip trend
        }
  
        // Dynamically set the trend multiplier based on the current trend
        const trendMultiplier = currentTrend === "up" ? 1 : -1;
  
        // Compute the daily change based on volatility and trend
        const dailyChange = avg * (1 + volatility * (Math.random() - 0.5)) * trendMultiplier;
  
        // Debugging logs
        // console.log({
        //   year,
        //   day,
        //   dailyChange,
        //   avg,
        //   volatility,
        //   currentTrend,  // Log the current trend
        //   currentPrice,
        // });
  
        // Calculate new prices
        const open = currentPrice;
        const close = open + dailyChange;
  
        // High and low of the day
        const high = Math.max(open, close);
        const low = Math.min(open, close);
  
        // Add the new data point
        stockArray.push([
          newDate, // Date
          open, // Open
          high, // High
          low, // Low
          close, // Close
        ]);
  
        currentPrice = close; // Update for the next day
      }
    }
  
    return stockArray.slice(-365);
  };
  
  
  const generateStockData = (initialStockData) => {
    const gameStartYear = 1981;
    const updatedStockData = { ...initialStockData };

    Object.keys(updatedStockData).forEach((key) => {
      const stock = stocks[key];
      const stockArray = updatedStockData[key];
      const startDate = stockArray[0][0];
      const startYear = new Date(startDate).getFullYear();

      updatedStockData[key] = generateDailyData(
        stockArray,
        startYear,
        gameStartYear,
        stock.avg,
        stock.volatility,
        stock.initialTrend
      );
    });

    return updatedStockData;
  };

  useEffect(() => {
    const initialData = createInitialStockData();
    setStockData(initialData);
    setCurrentStock(Object.keys(stocks)[0]); // Set the first stock as loading for display

    const completeData = generateStockData(initialData); // Generate data after initializing

    const copyGameState = {...gameState}
    const gameData = copyGameState.gameData
    gameData.stocks = completeData
    copyGameState.gameData = gameData
    localStorage.setItem(`stockGameSlot${gameState.chosenSlot}`, JSON.stringify(gameData));
    setGameState(copyGameState);
  }, []);


  return (
    <div>
      <h1>
        Currently Loading{" "}
        {currentStock ? stocks[currentStock].name : "Not loading"}
      </h1>
      <pre>{JSON.stringify(stockData, null, 2)}</pre>
    </div>
  );
};

export default GameLoading;
