import { useState, useEffect } from "react";

const GameStart = ({ gameState, setGameState }) => {
  const [newGameDetails, setNewGameDetails] = useState({
    name: "",
    isCheats: false,
    isTutorial: false,
  });

  const selectGame = (isNew) => {
    const copyGameState = { ...gameState };
    if (isNew) {
      copyGameState.isNewGame = true;
    } else {
      copyGameState.isNewGame = false;
    }

    setGameState(copyGameState);
  };

  const saveToSlot = (slotId) => {
    const gameData = {
      gameName: newGameDetails.name,
      isCheats: newGameDetails.isCheats,
      isTutorial: newGameDetails.isTutorial,
      day: 0,
      money: 1000,
      owned: [],
      stocks: []
    };

    setGameState({ ...gameState, chosenSlot: slotId, isGameStart: true, gameData: gameData });
    localStorage.setItem(`stockGameSlot${slotId}`, JSON.stringify(gameData));
  };

  const [slotNames, setSlotNames] = useState([]);
  const loadSaveSlotInfo = () => {
    const slotsAvailable = 3;
    const tempSlotNames = [];
    for (let i = 0; i < slotsAvailable; i++) {
      const slotNumber = i + 1;
      const isStorageExist = localStorage.getItem(`stockGameSlot${slotNumber}`);

      if (isStorageExist) {
        const makeData = JSON.parse(isStorageExist);

        tempSlotNames.push({
          name: makeData.gameName,
          day: makeData.day,
          money: makeData.money,
        });
      } else {
        tempSlotNames.push({ name: "Empty", day: 0, money: 0 });
      }
    }

    setSlotNames(tempSlotNames);
  };

  const loadSelectedSave = (slotId) => {
    const slotNumber = slotId + 1;
    const isStorageExist = localStorage.getItem(`stockGameSlot${slotNumber}`);

    if (isStorageExist) {
      const makeData = JSON.parse(isStorageExist);
      setGameState({
        ...gameState,
        isGameStart: true,
        chosenSlot: slotNumber,
        gameData: makeData,
      });
    } else {
      const copyGameState = { ...gameState };
      copyGameState.isNewGame = true;
      setGameState(copyGameState);
    }
  };

  useEffect(() => {
    loadSaveSlotInfo();
  }, []);

  return (
    <div>
      <h1>Game Title</h1>

      <div>
        {gameState.isNewGame === null ? (
          <div>
            <button onClick={() => selectGame(true)}>New</button>
            <button onClick={() => selectGame(false)}>Load</button>
          </div>
        ) : gameState.isNewGame ? (
          <div>
            <div style={{ display: "flex", flexDirection: "column" }}>
              <span>Game name</span>
              <input
                value={newGameDetails.name}
                onChange={(e) =>
                  setNewGameDetails({ ...newGameDetails, name: e.target.value })
                }
              />

              <div>
                <span>Tutorial</span>
                <input
                  type="checkbox"
                  checked={newGameDetails.isTutorial}
                  onChange={(e) =>
                    setNewGameDetails({
                      ...newGameDetails,
                      isTutorial: e.target.checked,
                    })
                  }
                />
              </div>

              <div>
                <span>Cheats</span>
                <input
                  type="checkbox"
                  checked={newGameDetails.isCheats}
                  onChange={(e) =>
                    setNewGameDetails({
                      ...newGameDetails,
                      isCheats: e.target.checked,
                    })
                  }
                />
              </div>

              <button onClick={() => saveToSlot(1)}>Start</button>
            </div>
          </div>
        ) : (
          <div style={{ display: "flex", gap: 15 }}>
            {slotNames &&
              slotNames.length > 0 &&
              slotNames.map((slot, index) => {
                return (
                  <button
                    style={{ minWidth: 200 }}
                    onClick={() => loadSelectedSave(index)}
                  >
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        textAlign: "left",
                      }}
                    >
                      <span>{slot.name}</span>
                      <span>${slot.money}</span>
                      <span>Days: {slot.day}</span>
                    </div>
                  </button>
                );
              })}
          </div>
        )}
      </div>
    </div>
  );
};

export default GameStart;
