import "./App.css";
import CandlestickWrapper from "./assets/components/CandleStickTemp/CandlestickWrapper";
import GameWrapper from "./assets/components/Game/GameWrapper";
import GameStart from "./assets/components/GameStart/GameStart";

import { useState } from "react";

function App() {
  const [gameState, setGameState] = useState({
    isGameStart: false,
    chosenSlot: null,
    isNewGame: null,
    gameData: null,
  });

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        height: "100vh",
        width: "100vw",
      }}
    >
      {!gameState.isGameStart ? (
        <GameStart gameState={gameState} setGameState={setGameState} />
      ) : (
        <GameWrapper gameState={gameState} setGameState={setGameState} />
      )}
    </div>
  );
}

export default App;
