import CandlestickWrapper from "../CandleStickTemp/CandlestickWrapper";
import { useEffect, useState, useRef } from "react";
import stocks from "./data/stocks";

const GameUI = ({ gameState, setGameState }) => {
  // Refresh the page if you change these
  const dayLength = 30000 // 30 seconds in milliseconds
  const eventChance = 0.15 // 15% as decimal 

  const [options, setOptions] = useState(null);
  const [selectedStockUI, setSelectedStockUI] = useState(null);
  useEffect(() => {
    if (options) {
      const copy = { ...options };
      const seriesCopy = copy.series;
      seriesCopy[0].data = [];
      copy.series = seriesCopy;
      setOptions(copy);
    }

    setTimeout(() => {
      if (gameState.gameData.stocks && selectedStockUI !== null) {
        const stockLoad = selectedStockUI;

        const selected = gameState.gameData.stocks[stockLoad];
        const selectedStock = stocks[stockLoad];
        const tempOptions = {
          chart: {
            type: "candlestick",
            zoomType: "x", // Allow zooming along the x-axis
            panning: {
              enabled: true, // Enable panning (drag-to-scroll)
              type: "x", // Only allow panning along the x-axis
            },
            panKey: "shift",
          },
          rangeSelector: {
            selected: 1, // Set default range (e.g., 1 week)
          },
          series: [
            {
              name: selectedStock.name,
              data: selected,
              upColor: "#00FF00", // Green for up (positive) candlesticks
              downColor: "red", // Red for down (negative) candlesticks
            },
          ],
          xAxis: {
            type: "datetime",
            labels: {
              formatter: function () {
                const date = new Date(this.value);
                return date.toLocaleDateString(); // Formats the date as 'MM/DD/YYYY'
              },
            },
          },
        };

        setOptions(tempOptions);
      }
    }, 100);
  }, [gameState.gameData.stocks, selectedStockUI]);

  const chartRef = useRef(null); // Ref for accessing the chart instance

  const pcLocations = ["Bank", "Market", "Portfolio"];

  const [selectedPCLocation, setSelectedPCLocation] = useState(null);

  const addMoney = () => {
    const copyGame = { ...gameState };
    copyGame.gameData.money = copyGame.gameData.money + 10000;
    setGameState(copyGame);
  };

  const [amountToBuy, setAmountToBuy] = useState(0);
  const [totalToBuy, setTotalToBuy] = useState(0);
  const totalAmount = () => {
    const currentPrice =
      gameState.gameData.stocks[selectedStockUI][
        gameState.gameData.stocks[selectedStockUI].length - 1
      ][4] * amountToBuy;
    const fixPrice = currentPrice.toFixed(2);
    setTotalToBuy(fixPrice);
  };

  useEffect(() => {
    if (amountToBuy !== 0 && selectedStockUI) totalAmount();
  }, [amountToBuy]);

  const purchaseStock = () => {
    if (!amountToBuy) {
      console.error("Enter a real amount");
      return;
    }

    if (amountToBuy == 0) {
      console.error("Enter an amount");
      return;
    }

    if (totalToBuy > gameState.gameData.money) {
      console.error("You cannot afford this");
      return;
    }

    const copyGame = { ...gameState };
    const copyOwned = [...copyGame.gameData.owned];
    const getSelected = gameState.gameData.stocks[selectedStockUI];

    const tempStock = {
      stockName: selectedStockUI,
      boughtAt: getSelected[getSelected.length - 1][4].toFixed(2),
      units: amountToBuy,
    };

    copyOwned.push(tempStock);
    copyGame.gameData.money = copyGame.gameData.money - totalToBuy;
    copyGame.gameData.money = Number(copyGame.gameData.money.toFixed(2));
    copyGame.gameData.owned = copyOwned;
    setGameState(copyGame);
    setAmountToBuy(0);
    setTotalToBuy(0);
  };

  const calculateProfitLoss = (boughtAt, priceNow) => {
    const profitLoss = priceNow - boughtAt;
    const formattedValue = `${profitLoss >= 0 ? "+" : ""}${profitLoss.toFixed(
      2
    )}`;
    return { value: formattedValue, color: profitLoss >= 0 ? "green" : "red" };
  };

  const sellStock = (stockIndex) => {
    const copyGame = { ...gameState };
    const copyOwned = [...copyGame.gameData.owned];

    // Get the stock details to sell
    const stockToSell = copyOwned[stockIndex];

    // Get the current price of the stock (latest price in the array)
    const getSelected = copyGame.gameData.stocks[stockToSell.stockName];
    const currentPrice = getSelected[getSelected.length - 1][4]; // Closing price of the stock

    // Calculate the total sale amount
    const saleAmount = stockToSell.units * currentPrice;

    // Remove the stock from owned
    copyOwned.splice(stockIndex, 1);

    // Update money and owned stocks in the game state
    copyGame.gameData.money += saleAmount;
    copyGame.gameData.money = Number(copyGame.gameData.money.toFixed(2));
    copyGame.gameData.owned = copyOwned;

    // Update the game state
    setGameState(copyGame);

    console.log(
      `Sold ${stockToSell.units} units of ${
        stockToSell.stockName
      } for ${saleAmount.toFixed(2)}.`
    );
  };

  const [events, setEvents] = useState([]);
  const eventsRef = useRef(events);

  const addEvent = () => {
    const e = {
      stock: "GLD",
      trend: "up",
      generations: 3,
      avg: 500,
      volatility: 0.7,
    };

    setEvents([...events, e]);
  };

  useEffect(() => {
    eventsRef.current = events;
  }, [events]);

  const generateMinuteData = (stockArray, avg, volatility, stockName) => {
    const lastEntry = stockArray[stockArray.length - 1];
    const lastDate = lastEntry[0];
    const newDate = lastDate + 24 * 60 * 60 * 1000; // Add 1 day (in milliseconds)
    let priceChange = 0;

    // Check for matching events
    const eventsT = eventsRef.current; // Use the ref to access the latest events
    const stockEventIndex = eventsT.findIndex(
      (event) => event.stock === stockName
    );

    if (stockEventIndex !== -1) {
      const stockEvent = eventsT[stockEventIndex];

      // Override parameters if event exists
      avg = stockEvent.avg;
      volatility = stockEvent.volatility;

      // Adjust trend if specified
      const trendMultiplier = stockEvent.trend === "up" ? 1 : -1;
      priceChange =
        avg * (1 + volatility * (Math.random() - 0.5)) * trendMultiplier;

      // Reduce event generations and remove if exhausted
      stockEvent.generations -= 1;
      if (stockEvent.generations <= 0) {
        setEvents((prev) =>
          prev.filter((_, index) => index !== stockEventIndex)
        );
      }
    } else {
      // Calculate the trend based on the last 3 entries
      const trendDirection = getTrendDirection(stockArray);

      // Decide if the trend will flip (20% chance to flip)
      const stockData = stocks[stockName];
      const willFlip = Math.random() < stockData.volatility;

      // Determine the final trend multiplier based on the flip and trend direction
      const trendMultiplier = willFlip ? -trendDirection : trendDirection;

      priceChange =
        avg * (1 + volatility * (Math.random() - 0.5)) * trendMultiplier;
    }

    const newPrice = Math.max(0, lastEntry[4] + priceChange); // Ensure price is non-negative
    const open = lastEntry[4]; // Previous close becomes the next day's open
    const close = newPrice;
    const high = Math.max(open, close);
    const low = Math.min(open, close);

    stockArray.push([
      newDate, // Date
      open, // Open
      high, // High
      low, // Low
      close, // Close
    ]);

    return stockArray.slice(-365); // Keep only the most recent year of daily data
  };

  // Helper function to determine the trend based on the last 3 entries
  const getTrendDirection = (stockArray) => {
    if (stockArray.length < 3) return 0; // No trend if not enough data

    const last3Entries = stockArray.slice(-3); // Get last 3 entries
    const priceChanges = [
      last3Entries[2][4] - last3Entries[1][4], // Change from entry 1 to 2
      last3Entries[1][4] - last3Entries[0][4], // Change from entry 2 to 1
    ];

    // Calculate overall trend direction
    const trendSum = priceChanges.reduce((acc, change) => acc + change, 0);

    // If the trend is upward, return 1, if downward return -1, if flat return 0
    if (trendSum > 0) return 1;
    if (trendSum < 0) return -1;
    return 0; // No clear trend
  };

  const updateMinuteData = () => {
    const updatedStocks = { ...gameState.gameData.stocks };

    Object.keys(updatedStocks).forEach((key) => {
      const stock = stocks[key];
      const stockArray = updatedStocks[key];

      updatedStocks[key] = generateMinuteData(
        stockArray,
        stock.avg,
        stock.volatility,
        key
      );
    });

    // Make sure the entire gameState is updated properly
    setGameState((prevState) => ({
      ...prevState,
      gameData: {
        ...prevState.gameData,
        stocks: updatedStocks,
      },
    }));

    const copyGame = { ...gameState.gameData };
    copyGame.stocks = updatedStocks;

    localStorage.setItem(
      `stockGameSlot${gameState.chosenSlot}`,
      JSON.stringify(copyGame)
    );

    rollEvent();
  };

  const rollEvent = () => {
    let stocksArr = [];
    Object.keys(stocks).forEach((key) => {
      stocksArr.push(key);
    });
    const isEvent = Math.random() < eventChance;
    console.log(isEvent);
    if (isEvent) {
      let cur = [...eventsRef.current];
      const availableStocks = stocksArr.filter((stockName) => {
        // Check if the stock already has an event in the current list of events
        return !cur.some((event) => event.stock === stockName);
      });

      if (availableStocks.length > 0) {
        const randomStock =
          availableStocks[Math.floor(Math.random() * availableStocks.length)];
        const newEvent = createEvent(randomStock);
        console.log(newEvent);
        setEvents([...events, newEvent]);
      }
    }
  };

  const createEvent = (stockName) => {
    const vol = Math.random();
    const trendVal = Math.random() * vol
    const trend = trendVal < 0.5 ? "up" : "down";
    console.log(trendVal)
    const avg = 30;

    return {
      stock: stockName,
      trend: trend,
      generations: 3,
      avg: avg,
      volatility: vol,
    };
  };

  useEffect(() => {
    if (gameState.isGameStart) {
      const interval = setInterval(() => {
        updateMinuteData();
      }, dayLength);

      return () => clearInterval(interval); // Cleanup on unmount
    }
  }, [gameState.isGameStart]);

  return (
    <div>
      {!selectedStockUI ? (
        <div>
          <div style={{ display: "flex", backgroundColor: "", gap: 25 }}>
            {pcLocations &&
              pcLocations.length > 0 &&
              pcLocations.map((pcL) => {
                return (
                  <div
                    style={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      cursor: "pointer",
                    }}
                    onClick={() => setSelectedPCLocation(pcL)}
                  >
                    <h3>{pcL}</h3>
                  </div>
                );
              })}
          </div>
          {selectedPCLocation === null ? (
            <span>Welcome </span>
          ) : selectedPCLocation === "Bank" ? (
            <div>
              <span onClick={addEvent}>Money: $</span>
              {gameState.gameData.money.toFixed(2)}{" "}
              {gameState.gameData.isCheats && (
                <button onClick={() => addMoney()}>Add Money</button>
              )}
            </div>
          ) : selectedPCLocation === "Market" ? (
            <div style={{ display: "flex", flexDirection: "column", gap: 10 }}>
              {Object.entries(gameState.gameData.stocks).map(([key, stock]) => {
                return (
                  <div
                    key={key}
                    onClick={() => setSelectedStockUI(key)}
                    style={{
                      cursor: "pointer",
                      border: "1px solid black",
                      borderRadius: 5,
                      padding: 5,
                      display: "flex",
                      justifyContent: "space-between",
                    }}
                  >
                    <div>{key}</div>
                    <div>
                      $
                      {gameState.gameData.stocks[key][
                        gameState.gameData.stocks[key].length - 1
                      ][4].toFixed(2)}
                    </div>
                  </div>
                );
              })}
            </div>
          ) : (
            <div>
              {gameState.gameData.owned && gameState.gameData.owned.length > 0
                ? gameState.gameData.owned.map((ownedStock, index) => {
                    return (
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "column",
                          cursor: "pointer",
                          border: "1px solid black",
                          borderRadius: 5,
                          padding: 5,
                        }}
                        onClick={() => sellStock(index)}
                      >
                        <p style={{ margin: 0 }}>
                          Stock: {ownedStock.stockName}
                        </p>
                        <p style={{ margin: 0 }}>Unit(s): {ownedStock.units}</p>
                        <p style={{ margin: 0 }}>
                          Bought At: ${ownedStock?.boughtAt}
                        </p>
                        <p style={{ margin: 0 }}>
                          Price now: $
                          {gameState.gameData.stocks[ownedStock.stockName][
                            gameState.gameData.stocks[ownedStock.stockName]
                              .length - 1
                          ][4].toFixed(2)}
                        </p>
                        <p style={{ margin: 0 }}>
                          {
                            calculateProfitLoss(
                              ownedStock.boughtAt,
                              gameState.gameData.stocks[ownedStock.stockName][
                                gameState.gameData.stocks[ownedStock.stockName]
                                  .length - 1
                              ][4].toFixed(2)
                            ).value
                          }
                        </p>
                      </div>
                    );
                  })
                : null}
            </div>
          )}
        </div>
      ) : (
        <div style={{ display: "flex", width: "100%" }}>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              gap: 10,
              backgroundColor: "",
              overflow: "auto",
            }}
          >
            {Object.entries(gameState.gameData.stocks).map(([key, stock]) => {
              return (
                <div
                  key={key}
                  onClick={() => setSelectedStockUI(key)}
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    cursor: "pointer",
                    border: "1px solid black",
                    minWidth: 70,
                    padding: 20,
                    borderRadius: 5,
                  }}
                >
                  {key}
                </div>
              );
            })}
            <div
              style={{
                display: "flex",
                justifyContent: "center",
                cursor: "pointer",
                border: "1px solid black",
                minWidth: 70,
                padding: 20,
                borderRadius: 5,
              }}
              onClick={() => setSelectedStockUI(null)}
            >
              Back
            </div>
          </div>
          <div>
            <CandlestickWrapper options={options} chartRef={chartRef} />
          </div>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <h3>{stocks[selectedStockUI].name}</h3>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <span>Price Per Unit: </span>
              <span>
                $
                {gameState.gameData.stocks[selectedStockUI][
                  gameState.gameData.stocks[selectedStockUI].length - 1
                ][4].toFixed(2)}
              </span>
            </div>
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                alignItems: "center",
              }}
            >
              <span style={{ marginTop: 10 }}>Units to purchase</span>
              <input
                style={{ width: "20%", marginTop: 10 }}
                value={amountToBuy}
                onChange={(e) => {
                  const value = e.target.value;
                  const numericValue = value.replace(/\D/g, "");
                  setAmountToBuy(numericValue);
                }}
              />
            </div>
            <span>Total Price: {totalToBuy}</span>
            <span>Balance: ${gameState.gameData.money}</span>
            <span>
              Balance after purchase: $
              {(gameState.gameData.money - totalToBuy).toFixed(2)}
            </span>
            <button onClick={purchaseStock}>Buy</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default GameUI;
